@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;700&display=swap');* { box-sizing: border-box; } 
img { vertical-align: middle; } 
ul { list-style: none; margin: 0; padding: 0; } 
a { text-decoration: none; color: #333; } 

.App { position: fixed; display:flex; top: 0; left: 0; width: 100%; height: 100%; background-color: #000; overflow: hidden; align-items: center; justify-content: center; } 
.Panel-Container { position: relative; display: flex; max-width: 900px; max-height: 700px; width: 100%; height: 100%; padding: 30px; flex-direction: column; flex: 2px; } 
.Panel-Row { display:flex; width: 100%; height: 100%; } 
.Panel-Row:nth-child(2) { height: 35%; } 
.Panel-Row:nth-child(n+2) { margin-top: 2px; } 
.Panel-Bundle { flex: 1 1; } 
.Panel-Bundle:nth-child(n+2) { margin-left: 2px; } 
.Panel-Grid { display: grid; width: 100%; height:100%; gap: 2px; } 
.Panel-Grid-3 { grid-template-columns: 1fr 1fr 1.5fr; } 
.Panel-Grid-4 { grid-template-rows: 1fr 1fr; grid-template-columns: 1fr 1fr; } 
.Panel { position: relative; width: 100%; height: 100%; background-color: #fff; border-radius: 10px !important; cursor: pointer; } 

.Panel.P1 {display: grid; padding: 30px 20px; grid-template-rows: 1fr; grid-template-columns: 1fr;}
.Panel.P1 .Text {display: flex; justify-content: center; align-items: center; font-size: 90px; font-weight: bold; letter-spacing: -8px; font-family: 'Roboto Condensed', sans-serif;}
.Panel.P1 .Text img {position: absolute; right: 23px; width: 85px; transform: rotate(17deg);}
.Panel.P1 .Description {line-height: 34px; padding-top: 20px; font-size: 36px; font-weight: 600; word-break: break-all; font-family: 'Roboto Condensed', sans-serif; letter-spacing: -2px; border-top: 1px solid #555;}
.Panel.P2 { background-image: linear-gradient(to bottom, rgb(231, 193, 176), rgb(214, 151, 135)); transform-style: "preserve-3d"; transform: rotateY(0deg); } 
.Panel.P2.flipped { transform: rotateY(180deg); } 
.Panel.P2 span { position: absolute; bottom: 10px; right: 10px; color: #fff; font-size: 16px; font-weight: 300; } 
.Panel.P2 img { position: absolute; top: 50%; left: 50%; max-height: 150px; height: 100%; transform: translate(-50%, -50%); } 
.Panel.P3 { background-color: #000; cursor: default; } 
.Panel.P3 ul { position: absolute; top:0; left:0; width: 100%; height: 100%; margin: 0; padding: 10px 30px; box-sizing: border-box; } 
.Panel.P3 li a { display: block; padding:10px 0; color: #cdcdcd; font-weight: 300; cursor: pointer; } 
.Panel.P3 li a:hover { color: #fff; } 
.Panel.P3 li:last-child { position: absolute; bottom: 10px; color: #fff; list-style: none; font-weight: 300; } 
.Panel.P4 { background-color: #000; } 
.Panel.P4 ul { margin: 0; padding: 0; list-style: none; } 
.Panel.P4 ul li { position: relative; width: 100%; height: 50px; line-height: 52px; background-color: #fff; border-radius: 5px; padding: 0 20px; } 
.Panel.P4 ul li:nth-child(1) { background-color: rgb(246, 202, 83); text-align: right; font-size: 12px; letter-spacing: 1px; } 
.Panel.P4 ul li:nth-child(1)::before { content: ""; position: absolute; top: calc(50% - 5px); display: block; width: 10px; height: 10px; border-radius: 50%; background-color: #000; } 
.Panel.P4 ul li:nth-child(2) { margin-top: 2px; transform: rotate(7deg); text-align: center; font-family: 'Roboto Condensed', sans-serif; } 
.Panel.P4 ul li:nth-child(2) a { font-size: 16px; } 
.Panel.P4 ul li:nth-child(3) { position: absolute; bottom: 0px; background-color: transparent; color: #fff; font-size: 20px; letter-spacing: -1px; font-family: 'Roboto Condensed', sans-serif; } 
.Panel.P5 { display: flex; justify-content: center; align-items: center; } 
.Panel.P5 ul { position: absolute; top: 0; left: 0; width: 100%; height: 100%; } 
.Panel.P5 ul li { position: absolute; color: #ddd; font-size: 50px; font-weight: 300; } 
.Panel.P5 ul li:nth-child(1) { top: 10px; left: 15px; } 
.Panel.P5 ul li:nth-child(2) { top: 10px; right: 15px; } 
.Panel.P5 ul li:nth-child(3) { bottom: 10px; left: 15px; } 
.Panel.P5 ul li:nth-child(4) { bottom: 10px; right: 15px; } 
.Panel.P5 p { display: inline-block; width: 55%; height: fit-content; max-height: 175px; margin: 0; overflow: hidden; word-break: break-all; font-weight: bold; line-height: 14px;} 

.Panel.P6 { background-color: transparent; background-image: url('../images/videogame1.png'); background-position: center; background-size: auto 70%; background-repeat: no-repeat; } 
.Panel.P6:hover { background-image: url('../images/videogame.png'); } 
.Panel.P7 { background: linear-gradient(to right, rgb(90, 128, 106), rgb(69, 107, 91)); overflow: hidden; } 
.Panel.P7 span { position: absolute; top: 10px; left: 10px; color: #fff; font-size: 10px; font-weight: 300; } 
.Panel.P7 span b { display: block; font-weight: 400; } 
.Panel.P7 img { position: absolute; top: 50%; left: 50%; max-height: 120px; height: 80%; transform: translate(-50%, -50%); } 
.Panel.P8 { display: flex; width: 100%; padding: 20px; flex-direction: column; background-image: url('../images/down-right.png'); background-position: calc(100% - 10px) calc(100% - 10px); background-size: 30px; background-repeat: no-repeat; } 
.Panel.P8 .Info-Summary { display: flex; font-size: 14px; } 
.Panel.P8 .Info-Summary:nth-child(2) { flex-direction: column; margin-top: 30px; } 
.Panel.P8 .Info-Job { display: inline-flex; justify-content: center; align-items: center; width: 40px; height: 55px; border: 1px solid #999; border-radius: 20px; font-size: 10px; } 
.Panel.P8 .Info-Image { display: inline-flex; width: 40px; height: 55px; background-color: #ddd; margin-left: 3px; border-radius: 20px; overflow: hidden; background-image: url('../images/my-profile.jpeg'); background-size: 300%; background-position: 62% 53%; } 
.Panel.P8 dl { margin:0; } 
.Panel.P8 dl:nth-child(2) { margin-top:5px; } 
.Panel.P8 dt { display: inline-block; width: 50px; color: #444; } 
.Panel.P8 dd { display: inline-block; width: calc(100% - 50px); margin: 0; font-size: 14px; color: #444; } 
.Panel.P8 .Info-sns { position: absolute; top: 13px; right: 11px; } 
.Panel.P8 .Info-sns ul { margin: 0; padding: 0; list-style: none; } 
.Panel.P8 .Info-sns ul li { float: right; } 
.Panel.P8 .Info-sns ul li:nth-child(1) { margin-right: -5px; } 
.Panel.P8 .Info-sns a { display: block; } 
.Panel.P8 .Info-sns a img { width: 35px; } 


.Modal { position: fixed; display: flex; top:0; left:0; width: 100%; min-height: 100%; align-items: center; justify-content: center; z-index: 99; } 
.Modal-Background { position: fixed; top:0; left:0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.6); } 
.Modal-Content { position: absolute; width: 300px; height: 300px; border-radius: 10px; background-color: #fff; z-index: 99; overflow-y: scroll; } 
.Modal-Content.loading { background-image: url('../images/cool_imoji.png'); background-position: center; background-size: 50px; background-repeat: no-repeat; } 

.Page { min-height: 100%; padding: 50px; } 
.Page-inner { max-width: 900px; width: 100%; margin: 0 auto; } 
.Page-inner h1 { border-bottom: 1px solid #ddd; padding-bottom: 15px; } 
.Page .Introduction .Cont p { margin-top: 60px; line-height: 26px; } 
.Page .Introduction .Cont dl { margin-top: 60px; } 
.Page .Introduction .Cont dt { color: #ddd; font-size: 40px; font-weight: bold; } 
.Page .Introduction .Cont dt:nth-child(n+2) { margin-top: 50px; } 
.Page .Introduction .Cont dd { margin:10px 0 0; padding: 0; line-height: 26px; } 

.Modal.P2 .Modal-Content { background-color: #000; } 
.Skills { position: absolute; top: 50%; left: 50%; height: fit-content; transform: translate(-50%, -50%); } 
.Skills span { display: flex; justify-content: center; align-items: center; color: rgba(255,255,255,0.5); font-weight: bold; cursor: unset; } 
.Skills .Panel-Row { height: inherit; } 

.Code dl:nth-child(n+2) { margin-top: 100px; } 
.code dl:nth-child(2n) dt { text-align: right; } 
.code dt { font-size: 60px; color: rgba(255,255,255,0.5); font-weight: bold; } 
.code dd { margin: 0; } 

.Career { padding: 50px 0; } 
.Page .Career { position: absolute; left: 50%; transform: translate(-50%, 0); } 
.Page .Career .Cont p { margin: 0; } 
.Page .Career .Cont dl { margin: 0; } 
.Page .Career .Cont dt { color: #333; } 
.Page .Career .Cont dd { padding-left: 20px; } 
.Page .Career .Cont .Tags { margin-top: 10 px; } 
.Page .Career .Cont .Tags:after { content: ""; display: block; clear: both; } 
.Page .Career .Cont .Tags span { position: relative; display: inline-block; height: 30px; line-height: 32px; padding: 0 15px 0 25px; background-color: #f3f3f3; border-radius: 20px; } 
.Page .Career .Cont .Tags span:nth-child(n+2) { margin-left: 5px; } 
.Page .Career .Cont .Tags span:before { content: "#"; position: absolute; left: 15px; } 
.Page .Career ul li { position: relative; padding-left: 60px; min-height: 48px; } 
.Page .Career ul li::before { content: ""; position: absolute; top: 63px; left: 15px; display: block; width: 15px; height: 15px; border-radius: 50%; background-color: #333; z-index: 1; } 
.Page .Career ul li::after { content: ""; position: absolute; top: 22px; left: 22px; display: block; width: 1px; height: calc(100% + 45px); background-color: #777; } 
.Page .Career ul li.Cont-SubTitle:first-child { padding-top: 0!important; } 
.Page .Career ul li.Cont-SubTitle:first-child::before { top: 14px; } 
.Page .Career ul li.Cont-SubTitle { color: #ddd; font-size: 45px; padding-top: 120px !important; font-weight: bold; } 
.Page .Career ul li.Cont-SubTitle::before { top: 133px; } 
.Page .Career ul li:last-child:after { display: none; } 
.Page .Career ul li:nth-child(n+2) { padding-top: 50px; } 